.main {
  overflow: hidden;
  height: 100vh;
  position: relative;
}
.content {
  width: 1200px;
  margin: 10px auto 0 auto;
}

.video {
  margin: 0;
  padding: 0;
  background: url (img, jpg) center center fixed no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1;
}

.introduce {
  margin-top: 10px;
  font-size: 1.25rem;
}

